  const res = await userTopReadService.getArtists(count, duration);
  isLogin.value = Object.keys(errorMessage).length === 0;
  // 判斷目前呼叫的 API 類型
  if (res) {
    apiHref.value = userTopReadService.getApiType(res?.data.href);
    artistsResult.value = res?.data?.items;
  }
}

